import { mapGetters, mapState } from 'vuex';
import catsImage from './cats-image.vue';

export default {
    name: "goods-list",
    props : [`goods_list`, `theme`],
    components: {
        catsImage
    },
    data() {
        return {
            previewUrl: '',
            submitUrl: '',
            attrGroup: [],
            selectAttr: {},
            item: {},
            show: 0
        }
    },
    computed: {
        ...mapGetters('mallConfig', {
            getVideo: 'getVideo',
            isShowCart: 'getShowCart'
        }),
        ...mapState({
            appSetting: state => state.mallConfig.mall.setting,
            appImg: state => state.mallConfig.__wxapp_img.mall,
            isListUnderlinePrice: state => state.mallConfig.mall.setting.is_list_underline_price,
            platform: function(state) {
                return state.gConfig.systemInfo.platform;
            }
        })
    },
    methods: {
        specification(goods) {
            if (goods.sign === 'mch') {
                this.previewUrl = this.$api.mch.order_preview;
                this.submitUrl = this.$api.mch.order_submit;
            } else {
                this.previewUrl = '';
                this.submitUrl = '';
            }
            uni.showLoading({
                text: '',
                mask: true
            });
            this.$request({
                url: this.$api.goods.attr,
                data: {
                    id: goods.id,
                    mch_id: goods.mch_id
                }
            }).then(e => {
                uni.hideLoading();
                if (e.code === 0) {
                    let data = Object.assign(goods, e.data);
                    this.$emit('attr', this.previewUrl, this.submitUrl, goods.attr_groups, data);
                } else {
                    uni.showToast({
                        title: e.msg,
                        icon: 'none'
                    })
                }
            })
        },

        route_go(item) {

            if (item.video_url && this.getVideo == 1) {

                uni.navigateTo({
                    url: `/pages/goods/video?goods_id=${item.id}&sign=${item.sign}`
                });






            } else {
                uni.navigateTo({
                    url: item.page_url
                });
            }







        },

        onAttr(data) {
            this.selectAttr = data;
        },
    }
}
